@import "reset";
@import "fonts";
@import "vars";
body {
    background-color: #eee;
}

.wrapper {
    width: 100%;
    max-width: 1080px;
    margin: 0 auto;
    padding-left: 1%;
    padding-right: 1%;
    // overflow-x: hidden;
    font-family: $font-primary;
    font-size: 15px;
}

sup {
    opacity: 0.6;
    font-size: 8px;
    vertical-align: super;
    a {
        color: inherit;
    }
}

.header {
    position: relative;
    padding-top: 25px;
    padding-bottom: 20px;
    background-color: $green;
    background-image: url("../img/header-bg.png"), url("../img/shadow-down.png");
    background-repeat: no-repeat;
    background-position-y: bottom;
    background-size: 100% 100%, 100% 100%;
    &:after {
        content: "";
        position: absolute;
        bottom: -17px;
        left: 11%;
        width: 45%;
        height: 31px;
        background: url("../img/shine.png") no-repeat 100% 100%;
        z-index: 10;
    }
    &__title-box {
        position: relative;
        width: 350px;
        height: 80px;
        margin: 0 auto;
        padding-top: 3px;
        background-image: url("../img/text_box.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        &:after {
            content: "";
            position: absolute;
            bottom: -10px;
            left: 0px;
            width: 350px;
            height: 15px;
            opacity: 0.7;
            background: url("../img/title-shade.png") no-repeat 100% 100%;
        }
    }
    &__title {
        font-family: 'futuramedium';
        font-size: 30px;
        text-align: center;
        line-height: 1.2;
        a {
            color: #fff;
        }
    }
    &__pollen {
        width: 24%;
        height: auto;
        position: absolute;
        bottom: -45px;
        z-index: 10;
    }
    @media only screen and (max-width: 768px) {
        .header__pollen,
        &:after {
            display: none!important;
        }
    }
    &__pollen--left {
        left: 1%;
    }
    &__pollen--right {
        right: 1%;
        -moz-transform: scaleX(-1);
        -o-transform: scaleX(-1);
        -webkit-transform: scaleX(-1);
        transform: scaleX(-1);
        filter: FlipH;
        -ms-filter: "FlipH";
    }
}

.menu {
    position: relative;
    min-height: 70px;
    background-color: $green;
    display: flex;
    justify-content: center;
    align-items: center;
    &:before {
        content: "";
        position: absolute;
        top: 10px;
        left: 0px;
        width: 100%;
        height: 15px;
        background: url("../img/shadow-under.png") no-repeat 100% 100%;
    }
    &__item {
        position: relative;
        font-family: 'futuralight';
        &:before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            height: 40px;
            width: 1px;
            background-image: url("../img/spacer.png");
            background-size: contain;
        }
    }
    .menu__item--wide {
        padding-left: 10px;
        padding-right: 10px;
        &:after {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            height: 40px;
            width: 1px;
            background-image: url("../img/spacer.png");
        }
    }
    .is-active {
        color: #01453e;
    }
    &__link {
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
        min-width: 115px;
        min-height: 40px;
        line-height: 1.2;
        color: #fff;
        transition: color 0.3s ease;
        &:hover {
            color: #01453e;
        }
    }
    &__dropdown {
        visibility: hidden;
        height: 0px;
        position: absolute;
        width: 150px;
        padding-top: 10px;
        padding-bottom: 5px;
        opacity: 0;
        background-color: $green;
        transition: all .3s ease-in-out;
    }
    .menu__item:hover .menu__dropdown {
        height: 150px;
        opacity: 1;
        visibility: visible;
        display: flex;
        flex-direction: column;
        z-index: 1000;
        .menu__item--dropdown {
            opacity: 1;
            transition-delay: 0.3s;
            transition: opacity .2s ease-in-out;
        }
    }
    .menu__item--dropdown {
        display: flex;
        padding: 10px 10px;
        color: #fff;
        opacity: 0;
        border-bottom: 1px solid #ccc;
        &:hover {
            background-color: darken($green, 10%)
        }
    }
}

// MAIN CONTENT
.owl-item {
    -webkit-box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
    box-shadow: inset 0px 25px 47px 0px rgba(0, 0, 0, 0.75);
}

//FOOTER
.footer {
    position: relative;
    height: 90px;
    padding: 10px;
    margin-bottom: 10px;
    background-color: $green;
    -webkit-box-shadow: 0px 10px 0px 0px #fba51a;
    -moz-box-shadow: 0px 10px 0px 0px #fba51a;
    box-shadow: 0px 10px 0px 0px #fba51a;
    border-bottom: 6px solid #dcdb88;
    &__icons-bar {
        text-align: center;
        margin: 0 auto;
    }
    &__references {
        position: absolute;
        right: 20px;
        bottom: 10px;
        font-family: 'advent-Lt1', Calibri, sans-serif;
        letter-spacing: 0.5px;
        color: #fff;
        transition: color 0.3s ease;
        &:hover {
            color: #01453e;
        }
    }
    &__keeping {
        position: absolute;
        right: 5%;
        top: 5px;
        width: 180px;
        height: 40px;
        padding-top: 3px;
        font-size: 18px;
        font-family: 'futuramedium';
        line-height: 1.1;
        background-image: url("../img/text_box.png");
        background-repeat: no-repeat;
        background-size: 100% 100%;
        &:after {
            content: "";
            position: absolute;
            bottom: -10px;
            left: 0px;
            width: 180px;
            height: 15px;
            opacity: 0.7;
            background: url("../img/title-shade--small.png") no-repeat 100% 100%;
        }
    }
    .footer__title {
        text-align: center;
        color: #fff;
    }
}

// PAGE: WHAT ARE ALLERGY
.content {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #fff;
    &__cat {
        margin-right: 20px;
        align-self: flex-end;
    }
    @media only screen and (max-width: 700px) {
        &__cat {
            order: 2;
        }
    }
    .content__text {
        max-width: 425px;
    }
    .content__title {
        margin-top: 50px;
        font-family: "futuramedium";
        font-size: 36px;
        color: $green;
    }
    p {
        margin-top: 20px;
        margin-bottom: 10px;
        line-height: 1.5;
        font-weight: 300;
        color: #58595b;
    }
    .content__list {
        margin-left: 15px;
        list-style-type: disc;
    }
    .content__list-item {
        line-height: 1.5;
        font-weight: 300;
        color: #58595b;
    }
    .content__tips-link {
        padding: 2px 5px;
        font-size: 12px;
        font-weight: 300;
        color: #fff;
        background-repeat: no-repeat;
        background-size: 100% 100%;
    }
    .content__cat-bottom {
        position: absolute;
        bottom: 0;
        right: 3%;
    }
    &__woman {
        position: absolute;
        right: 0;
    }
}

@media only screen and (max-width: 1079px) {
    .content {
        justify-content: center;
    }
    .content__woman {
        display: none;
    }
}

// PAGE: Allergy Symptoms
.content--symptoms {
    .symptoms__text {
        margin-left: 6%;
    }
    .content__title {
        margin-bottom: 40px;
        font-size: 38px;
        font-family: "futuralight";
        color: #58595b;
    }
}

// PAGE: ALLERGY TRIGGERS
.content--triggers {
    flex-direction: column;
    .content__title {
        margin-top: 30px;
        margin-bottom: 25px;
        font-family: "futuralight";
    }
    .triggers__text {
        padding-left: 30px;
    }
    p {
        margin-top: 10px;
        margin: 0;
    }
    .triggers__allergens {
        display: flex;
        align-items: center;
        padding-bottom: 30px;
    }
    .triggers__picture-box {
        position: relative;
        display: flex;
        align-items: center;
        &:before {
            position: absolute;
            content: "";
            height: 50px;
            width: 100%;
            top: 36%;
            background-color: rgba(0, 0, 0, 0.2);
            z-index: 0;
        }
    }
    .triggers__picture {
        width: 123px;
        height: 123px;
        margin-right: 15px;
        border: 15px solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        z-index: 10;
        &:first-child {
            margin-left: 30px;
        }
        &:last-child {
            margin-right: 80px;
        }
        img {
            width: 100%;
            height: 100%;
            border: 1px solid #fff;
            border-radius: 50%;
        }
    }
    .triggers__list {
        margin-bottom: 10px;
        padding: 10px;
        padding-bottom: 30px;
        border: 10px solid rgba(0, 0, 0, 0.2);
        li {
            list-style-type: disc;
        }
    }
    .triggers__allergens-title {
        margin-top: 20px;
        margin-bottom: 20px;
        font-size: 22px;
        font-weight: 500;
        color: #f99a05;
        sup {
            font-weight: 300;
            font-size: 12px;
        }
    }
    .triggers__allergen-item {
        margin-left: 30px;
        line-height: 1.5;
        font-size: 18px;
        font-weight: 300;
        color: #58595b;
    }
}

// PAGE: TYPES OF ALLERGY
.content--types {
    justify-content: center;
    padding-bottom: 15px;
    -webkit-box-shadow: inset 0px 40px 44px -30px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: inset 0px 40px 44px -30px rgba(0, 0, 0, 0.75);
    box-shadow: inset 0px 40px 44px -30px rgba(0, 0, 0, 0.75);
    .types__text {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: flex-end;
    }
    .list-box {
        max-width: 290px;
    }
    .content__list-title {
        margin-bottom: 15px;
        color: $green;
    }
    .content__list-item {
        margin-left: 10px;
        padding-left: 5px;
    }
    .list-box--center {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-left: 40px;
        margin-right: 60px;
        background-repeat: no-repeat;
        background-size: cover;
        .content__list-title {
            margin-top: 45px;
            padding-left: 15px;
            padding-right: 15px;
            line-height: 1.2;
            font-size: 20px;
            color: #58595b;
            font-weight: 500;
            text-align: center;
        }
    }
    .types__cat-point-box {}
    .types__bottom-title {
        font-size: 20px;
        font-weight: 500;
        color: $green;
    }
}

// PAGE: SKIN ALLERGIES
.content--skin {
    padding-bottom: 120px;
    padding-left: 40px;
    .skin__title {
        margin-top: 45px;
        font-size: 24px;
        font-weight: 500;
        color: $green;
    }
    .skin__desc {
        max-width: 85%;
    }
    .skin__desc--last {
        max-width: 80%;
    }
}

//PAGE: ANTIHISTAMINES
.content--anti {
    padding-top: 35px;
    padding-left: 40px;
    padding-bottom: 60px;
    .anti__title {
        margin-top: 25px;
        margin-bottom: 25px;
        font-size: 24px;
        color: #f99a05;
        font-weight: 500;
    }
    .anti__list-title {
        margin-bottom: 5px;
        font-weight: 400;
        color: #000;
    }
    .anti__number-line--1 {
        margin-top: 5px;
        margin-bottom: 5px;
    }
    .anti__line--orange {
        color: #f99a05;
        margin-bottom: 5px;
    }
    .content__list-item {
        margin-left: 3px;
        padding-left: 5px;
    }
    .anti__number-line--2 {
        max-width: 75%;
    }
}

// PAGE: TIPS
.content--tips {
    padding: 25px 0 60px 40px;
    .tips__text {
        display: flex;
        justify-content: space-between;
    }
    .tips__column1 {
        width: 55%;
        padding-right: 10%;
        box-sizing: border-box;
    }
    .tips__spacer {
        height: 330px;
        align-self: center;
    }
    .tips__column2 {
        width: 45%;
        padding-left: 5%;
        padding-top: 7%;
        box-sizing: border-box;
    }
    .tips__title {
        margin-bottom: 25px;
        font-size: 24px;
        font-family: "futuralight";
        color: #58595b;
        font-weight: 700;
    }
    .tips__subtitle {
        margin-bottom: 15px;
        line-height: 1.5;
        font-size: 16px;
        font-weight: 700;
        color: $green;
    }
    .content__list-item {
        span {
            font-weight: 500;
        }
        p {
            margin-top: 0;
            font-weight: 300;
        }
    }
    .content__tips-link {
        margin-left: 10px;
    }
    .skin-allergy-img {
        position: absolute;
        top: 0;
        right: 0;
    }
    .content__cat-btm {
        right: 0%;
    }
}

.tips__skin {
    .tips__text {
        flex-direction: column;
    }
    .tips__title {
        font-size: 30px;
        font-family: 'futuralight';
        color: #58595b;
        margin: 20px 0 30px 5px;
    }
    .tips__subtitle {
        font-size: 24px;
        font-weight: 500;
        margin-bottom: 10px;
    }
    .content__list-item {
        margin-bottom: 20px;
    }
}

// PAGE: REFERENCE 
.content--ref {
    .ref__text {
        padding-left: 40px;
        padding-bottom: 60px;
        font-size: 13px;
        color: #58595B;
    }
    .ref__logo {
        max-width: 144px;
        margin-top: 25px;
        margin-bottom: 25px;
    }
    .ref__subtitle {
        margin-bottom: 15px;
        font-weight: 700;
    }
    .ref__list-item {
        margin-bottom: 10px;
    }
    .reference__number {
        margin-right: 2px;
        font-weight: 700;
    }
}

@import "media";